/*============================================================================
	#Global
==============================================================================*/
html {
    box-sizing: border-box;
    height: 100%;
    scroll-behavior: smooth;
}

*,
*:before,
*:after {
    box-sizing: inherit;
}
p{
    margin: 0;
}
.lazy-background {
    background: inherit !important;
}
body {
    height: 100%;
    font-family: 'Inter', sans-serif;
    animation: 0.5s;
    overflow-x: hidden;
    color: #0B0B0B;
    max-width: 1920px;
    margin: 0 auto !important;

}
.root.overlay{
    transform: inherit !important;
}
.no-scroll {
    overflow: hidden;
    position: relative;
}
.no-scroll-popup {
    overflow: hidden;
    position: relative;
}

img {
    height: auto;
    max-width: 100%;
}

svg {
    vertical-align: top;
}
.wrapper {
    margin: 0 auto;
    max-width: 1920px;
}
.container {
    max-width: 1140px;
    padding: 0;
    margin: 0 auto;
    width: 100%;
}
html {
    box-sizing: border-box;
    height: 100%;
    width: 100vw;
    margin-right: calc(-1 * (100vw - 100%));
}

.no-scroll-popup {
    position: static;
    height: auto;
    overflow-y: hidden;
    @media (max-width: 1199.98px) {
        position: relative;
        height: inherit;
        overflow: hidden;
    }
}

a{
    text-decoration: inherit;
    &:hover {
        cursor: pointer;
    }
}


//fancybox
.compensate-for-scrollbar {
    margin: 0 auto !important;
}
body.compensate-for-scrollbar {
    overflow: hidden;
}
.lazy-background {
    background: inherit !important;
}
//fancybox

@media (max-width: 1199.98px) {
    .container {
        padding: 0 15px;
        max-width: 100%;
    }
    .root {
        margin-top:0;
    }
}
