
.input:-webkit-autofill,
.input:-webkit-autofill:hover,
.input:-webkit-autofill:focus,
.textarea:-webkit-autofill,
.textarea:-webkit-autofill:hover,
.textarea:-webkit-autofill:focus {

    -webkit-text-fill-color: inherit;
    -webkit-box-shadow: 0 0 0 30px white inset !important;
    background: #fff;
    transition: none;
}

.input[type="text"],
.input[type="email"],
.input[type="url"],
.input[type="password"],
.input[type="search"],
.input[type="number"],
.input[type="tel"],
.input[type="range"],
.input[type="date"],
.input[type="month"],
.input[type="week"],
.input[type="time"],
.input[type="datetime"],
.input[type="datetime-local"],
.input[type="color"],
.textarea {
    padding: 2px 12px;
    border-radius: 0;
}

.input[type="text"]:focus,
.input[type="email"]:focus,
.input[type="url"]:focus,
.input[type="password"]:focus,
.input[type="search"]:focus,
.input[type="number"]:focus,
.input[type="tel"]:focus,
.input[type="range"]:focus,
.input[type="date"]:focus,
.input[type="month"]:focus,
.input[type="week"]:focus,
.input[type="time"]:focus,
.input[type="datetime"]:focus,
.input[type="datetime-local"]:focus,
.input[type="color"]:focus,
.textarea:focus {
    outline: none;
}

.input[type="search"]::-webkit-search-decoration,
.input[type="search"]::-webkit-search-cancel-button,
.input[type="search"]::-webkit-search-results-button,
.input[type="search"]::-webkit-search-results-decoration {
    -webkit-appearance: none;
}

.input,
.textarea {
    padding: 11px 10px 14px 17px;
    border: 0;

}

.input:focus::-webkit-input-placeholder {
    color: transparent;
}

.input:focus:-moz-placeholder {
    color: transparent;
}

.input:focus::-moz-placeholder {
    color: transparent;
}

.input:focus:-ms-input-placeholder {
    color: transparent;
}

/* Remove Number From Inputs */
.input[type=number]::-webkit-inner-spin-button,
.input[type=number]::-webkit-outer-spin-button,
.input[type=number] {
    -webkit-appearance: none;
    -moz-appearance: textfield;
    appearance: none;
    margin: 0;
}


input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus textarea:-webkit-autofill,
textarea:-webkit-autofill:hover textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
    -webkit-box-shadow: 0 0 0px 1000px #ffffff inset !important;
}


input {
    box-shadow: none;
    outline: none;
    font-variant-numeric: lining-nums;
    border-radius: 0;
}



input[type=number]::-webkit-outer-spin-button,
input[type=number]::-webkit-inner-spin-button {
    -webkit-appearance: none;
}

input[type=number]:invalid,
input[type=number]:out-of-range {
    //border: 2px solid #ff6347;
}


input:active, input:hover, input:focus {
    outline: none;
    outline-offset: 0;
}

input {
    outline: none !important;
    webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    -webkit-box-shadow: none;
    -webkit-appearance: none;
}


// убрать заливку текста при клике

.filter__tabs_mobile, .filter__tabs_mobile label, .tab-name, .tab-name input,
.ss-main .ss-multi-selected .ss-values .ss-disabled, .ss-main .ss-multi-selected, .ss-main .ss-single-selected {
    -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
    -webkit-focus-ring-color: rgba(0, 0, 0, 0);
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

input:focus{
    background: #F0F0F0;
}
