.main-form textarea {
  padding: 16px 24px;
  width: 100%;
  height: 100%;
  max-width: 100%;
  font-size: 14px;
  line-height: 24px;
  letter-spacing: 0.04em;
  color: #091033;
  min-height: 110px;
  min-width: 100%;
  background: #FFFFFF;
  border: 1px solid #2D5FA4;
  border-radius: 10px;
  box-sizing: border-box;
  max-height: 80px;
  outline: none;
}

.main-form textarea:active, .main-form textarea:focus {
  outline: none;
}

.main-form.home-form input {
  background: inherit;
  border: 0;
  border-bottom: 1px solid #A196F3;
  height: 24px;
  color: #fff;
  font-size: 14px;
  line-height: 24px;
  width: 100%;
  padding: 4px 5px 8px 24px;
}

.form__row {
  margin-bottom: 24px;
}

.is-invalid {
  border-color: #dc3545;
  padding-right: calc(1.5em + 0.75rem);
  background-repeat: no-repeat;
  background-position: right calc(0.375em + 0.1875rem) center;
  background-size: calc(0.75em + 0.375rem) calc(0.75em + 0.375rem);
}

.main-form input::placeholder {
  display: flex;
  align-items: center;
  color: #FFF;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 26px;

}

.main-form textarea {
  font-weight: 400;
  display: flex;
  align-items: center;
  font-style: normal;
  font-size: 15px;
  line-height: 20px;
  color: #084470;
  background: #EAF3F9;
  border-radius: 15px;
  max-height: 103px;
  border: 0;

  &::placeholder {
    font-weight: 400;
    display: flex;
    align-items: center;
    font-style: normal;
    font-size: 15px;
    line-height: 20px;
    color: #084470;

  }
}

.just-validate-error-label {
  position: absolute;
  bottom: -18px;
  left: 26px;
  color: #7339F5 !important;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 16px;
}

.form-control {
  position: relative;
  svg{
    position: absolute;
    top: 0;
    left: 0;
    width: 16px;
    height: 16px;
  }
}
.main-form.home-form .form-control svg{
  top: 6px;
}
.form-control textarea:focus ~ .form-label, .form-control textarea.error ~ .form-label {
  top: -10px;
  left: 8px;
  font-weight: 500;
  font-size: 13px;
  line-height: 1.375em;
  color: #8E8E8E;
}

.form-control input.fix ~ .form-label {
  top: -10px;
  left: 8px;
  font-weight: 500;
  font-size: 13px;
  line-height: 1.375em;
  color: #8E8E8E;
}

.form-control .just-validate-error-field ~ .form-label {
  top: -10px;
  left: 8px;
  font-weight: 500;
  font-size: 13px;
  line-height: 1.375em;
  color: #8E8E8E;
}


.form-control .form-label {
  position: absolute;
  width: auto;
  left: 19px;
  top: 11px;
  white-space: nowrap;
  text-overflow: ellipsis;
  pointer-events: none;
  transform-origin: 0px 0px;
  font-size: 16px;
  line-height: 1.375em;
  padding: 0px;
  overflow: hidden;
  transition: all 0.5s ease 0s;
  font-weight: 400;
  color: #8E8E8E;
}


.form em.error {
  font-size: 12px;
  line-height: 18px;
  position: absolute;
  left: 26px;
  display: inline-block;
  width: 100%;
  margin-top: 0;
  top: 48px;
  padding-left: 0;
  color: red;
  font-style: normal;
}

.form__row {
  position: relative;
}

@media (max-width: 1023px) {
  .main-form textarea {
    min-height: 75px;
  }
}

@media (max-width: 767px) {
  .form em.error {
    top: 38px;
    font-size: 11px;
  }
}

@media (max-width: 480.98px) {
  .form-control input.fix ~ .form-label, .form-control input:focus ~ .form-label, .form-control input.error ~ .form-label, .form-control textarea.valid ~ .form-label {
    font-size: 12px;
  }
  .main-form textarea {
    font-size: 16px;
  }
  .form-control textarea:focus ~ .form-label, .form-control textarea.error ~ .form-label {
    font-size: 12px;
  }
  .just-validate-error-label {
    position: absolute;
    bottom: -19px;
    font-size: 9px;
  }

}
