/*============================================================================
	#Typography
==============================================================================*/
html {
  font-size: 100%;
}

body,
button,
input,
select,
optgroup,
textarea {
  color: #0B0B0B;
  font-size: 1rem;
  line-height: 1.625rem;
  font-weight: 400;
  letter-spacing: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
}


h1,
h2,
h3,
h3,
h4,
h5,
h6 {
  margin: 0;
}

h1 {
  font-size: 3rem;
  line-height: 3.5rem;
}

h2 {
  font-weight: 300;
  font-size: 2.625rem;
  line-height: 3.0625rem;
  margin: 56px 0;
}

h3 {
  font-weight: 500;
  font-size: 24px;
  line-height: 33px;
  color: #0B1F2F;
  margin: 2.5rem 0;
}

h4 {
  font-weight: 500;
  font-size: 1.625rem;
  line-height: 1.875rem;
  margin: 2.5rem 0;
}

h5 {
  font-weight: 500;
  font-size: 1.25rem;
  line-height: 1.625rem;
  margin: 2.5rem 0;
}

h6 {
  font-size: 1.125rem;
  line-height: 1.625rem;
  color: #0B1F2F;
  margin: 2.5rem 0;
  font-weight: 500;

}

.typography {
  h1 {
    font-size: 3rem;
    line-height: 3.5rem;
    margin: 2.5rem 0;
  }

  h2 {
    font-weight: 300;
    font-size: 2.625rem;
    line-height: 3.0625rem;
    margin: 2.5rem 0;
  }

  h3 {
    font-size: 1.5rem;
    line-height: 2.063rem;
  }

  h4 {
    font-size: 1.325rem;
    line-height: 1.875rem;
  }

  h5 {
    font-size: 1.25rem;
    line-height: 1.625rem;
  }

  h6 {
    font-size: 1.125rem;
    line-height: 1.625rem;
  }
}

.typography > *:last-child {
  margin-bottom: 0;
}

.table-scroll {
  overflow-x: auto;
  margin: 20px 0;
}

.typography p,
.typography ul li,
.typography ol li {
  color: #0B1F2F;
  font-weight: 400;
}

.typography strong {
  font-weight: 700;
}


.typography iframe {
  width: 100%;
  height: 422px;
  margin: 33px 0;
}


blockquote, .typography blockquote {
  position: relative;
  font-weight: 400;
  font-size: 1.25rem;
  line-height: 1.25rem;
  color: #C4895F;
  margin: 60px 0 53px;
  padding: 48px 0 1.875rem;
  border-bottom: 2px solid #C4895F;
  border-top: 2px solid #C4895F;

  p {
    font-weight: 400;
    font-size: 1.25rem;
    line-height: 1.625rem;
    color: #0B1F2F;
  }
}


blockquote:after, .typography blockquote:after {
  position: absolute;
  bottom: 8px;
  left: 200px;
  width: 115px;
  height: 2.5rem;
  background-image: url("/assets/icons/quotes.svg");
  background-position: center;
  background-repeat: no-repeat;
  content: "";
  background-color: #fff;
  top: -1rem;
}


.typography p {
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 26px;
  margin-bottom: 20px;
}
.typography p strong{
  color: #0B0B0B;
  font-size: 24px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}
.typography p a {
  font-size: 1.125rem;
  line-height: 1.8125rem;
  border-bottom: 8px solid rgba(196, 137, 94, 0.4);
  color: rgba(196, 137, 95, 1);
  text-decoration-line: inherit;

  &:hover {
    text-decoration-line: inherit;
  }

}

.typography a {
  font-size: 1.125rem;
  line-height: 1.8125rem;
  text-decoration-line: inherit;
  border-bottom: 8px solid rgba(196, 137, 94, 0.4);
  color: rgba(196, 137, 95, 1);

  &:hover {
    text-decoration-line: inherit;
  }
}


.typography blockquote p:last-child {
  margin: 0;
}

.typography p img {
  max-width: 100%;
  margin-bottom: 1.875rem;
  margin-top: 2.5rem;
}

.typography iframe {
  max-width: 100%;
  height: 420px;
}

.typography img {
  max-width: 100%;
  margin-bottom: 17px;
  margin-top: 32px;
  @media (max-width: 767.98px) {
    margin-top: 24px;
  }
}

.typography ul {
  list-style: none;
  padding: 0;
  margin: 56px 0;
}

.typography ul li {
  color: #000;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 26px;
  margin-bottom: 10px;
  position: relative;
  padding-left: 33px;
}

.typography ul li:before {
  position: absolute;
  top: 10px;
  left: 10px;
  width: 8px;
  height: 8px;
  background: #7339F5;
  content: '';
  border-radius: 50%;
}

.typography ol {
  list-style: none;
  counter-reset: my-counter;
  padding: 0;
  margin: 0 0 47px;
}

.typography ol li {
  counter-increment: my-counter;
  font-style: normal;
  color: #000;
  font-size: 16px;
  font-weight: 400;
  line-height: 26px;
  margin-bottom: 9px;
  position: relative;
  padding: 0 0 0 33px;
}

.typography ol li::before {
  content: counter(my-counter) ". ";
  font-style: normal;
  color: #7339F5;
  font-size: 20px;
  font-weight: 600;
  line-height: normal;
  top: 0;
  left: 0;
  position: absolute;
}

// seo
.seo-block {
  padding-top: 2.5rem;
  max-width: 1140px;
  margin: 0 auto 2.5rem;

  &__content {
    margin: 0 auto;
    max-width: 1140px;
    position: relative;
    overflow: hidden;
  }
}

.seo-block__addition {
  .seo-block__change {
    height: 280px;
    overflow: hidden;
    transition: height 1s;

    &:before {
      content: "";
      position: absolute;
      top: 11.875rem;
      left: 0;
      width: 100%;
      height: 296px;
      background-image: url(/assets/images/home/seo/bg.png);
      transition: opacity 1s;
    }
  }
}

.seo-block__change.open {
  &:before {
    opacity: 0;
  }
}

.seo-block__events.open .seo-block__hide {
  display: none;
}

.seo-block__events.close .seo-block__more {
  display: none;
}

.seo-block__events.close .seo-block__hide {
  display: block;
  padding-top: 1.125rem;
  text-transform: uppercase;
  color: #0b1f2f;
  font-weight: 600;
  font-size: 0.813rem;
  line-height: 0.125rem;

  &:hover {
    cursor: pointer;
  }
}

.seo-block__events .seo-block__more {
  font-style: normal;
  font-weight: 600;
  font-size: 0.813rem;
  line-height: 0.125rem;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #0b1f2f;
  margin-top: 15px;

  &:hover {
    cursor: pointer;
  }
}

.seo-block__more {
  img {
    margin: 3px;
  }
}

.seo-block {
  padding-top: 2.5rem;
  max-width: 1140px;
  margin: 0 auto 80px;
}

.video-block {
  width: 100%;
  overflow: hidden;
  position: relative;
  height: 100%;
  margin: 20px 0px 20px 0px;
}

.yt-player {
  background-repeat: no-repeat;
  background-position: center;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  aspect-ratio: 16 / 9;
  background-size: contain;

  &:hover {
    cursor: pointer;
  }

  //padding-top: 56.25%;
}

.yt-player__button {
  width: 65px;
  height: 50px;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transition: opacity .25s cubic-bezier(0, 0, .2, 1);
  -o-transition: opacity .25s cubic-bezier(0, 0, .2, 1);
  transition: opacity .25s cubic-bezier(0, 0, .2, 1);
  z-index: 1;
  background: inherit;
  border: 0;
  background-image: none;
  transform: translate(-50%, -50%);
  padding: 0;

  &:hover {
    cursor: pointer;
  }
}

.yt-player__button svg {
  width: 65px;
  height: 50px;
}

.yt-player iframe {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: -1;
}

.yt-player.video iframe {
  z-index: 1;
}

@media (max-width: 1199px) {

  .typography p, .typography ol li, .typography ul li {
    font-size: 1rem;
  }
  blockquote:before, .typography blockquote:before {
    left: -2.5rem;
  }
  blockquote:after, .typography blockquote:after {
    left: 80px;
  }
}

@media (max-width: 1023px) {


  .typography h1 {
    font-size: 2.25rem;
    line-height: 2.875rem;
  }
  .typography h2 {
    font-size: 2.25rem;
    line-height: 2.5rem;
  }
  .typography h3 {
    font-size: 2rem;
    line-height: 2.5rem;
  }
  .typography h4 {
    font-size: 1.875rem;
    line-height: 2.25rem;
  }
  blockquote {
    font-size: 1rem;
    line-height: 1.625rem;
  }
  .typography p a {
    font-size: 0.875rem;
  }
}


@media (max-width: 767px) {
  .seo-block {
    padding: 2.5rem 1.125rem;
    margin-bottom: 1.125rem;
  }
  .typography ul {
    margin: 1.875rem 0px 1.875rem 0;
  }
  .typography h1 {
    font-size: 1.875rem;
    line-height: 2.25rem;
  }

  .typography h2 {
    font-size: 1.625rem;
    line-height: 2.25rem;
  }

  .typography h3 {
    font-size: 24px;
    line-height: 1.875rem;
  }

  .typography h4 {
    font-size: 1.125rem;
    line-height: 24px;
  }

  .typography h5 {
    font-size: 0.125rem;
    line-height: 24px;
  }

  .typography ol li::before {
    font-size: 0.875rem;
    line-height: 0.125rem;
    top: 12px;
  }
  iframe {
    height: 250px;
  }
  blockquote, .typography blockquote, .typography h6, blockquote h3 {
    font-size: 1rem;
    line-height: 1.625rem;
    margin: 2.5rem 0 2.5rem;
  }
  blockquote p, .typography blockquote p {
    font-size: 1rem;
    line-height: 1.5rem;
  }
}

@media (max-width: 480px) {
  blockquote:before, .typography blockquote:before, blockquote:after, .typography blockquote:after {
    width: 60px;
    height: 60px;
  }
  blockquote p, .typography blockquote p {
    font-size: 1rem;
    line-height: 1.625rem;
  }
  .typography h1 {
    font-size: 1.875rem;
    line-height: 2.25rem;
  }

  .typography h2 {
    font-size: 1.625rem;
    line-height: 2.25rem;
  }

  .typography h3 {
    font-size: 24px;
    line-height: 1.875rem;
  }

  .typography h4 {
    font-size: 0.125rem;
    line-height: 24px;
  }

  .typography h5 {
    font-size: 1rem;
    line-height: 24px;
  }

  iframe {
    height: 250px;
  }
  .typography ol li {
    padding: 0 0 0 1.875rem;
  }
  .typography h2, .typography h3, .typography h4, .typography h5 {
    margin: 35px 0 1.125rem;
  }
  blockquote {
    margin: 1.875rem 0;
    padding: 1.125rem 0;
  }
  .typography ul li:before {
    top: 8px;
    left: 0;
  }
  .typography a, .typography ol li, .typography p, .typography p a, .typography ul li {
    font-size: 14px;
    line-height: 18px;
  }
  .typography iframe {
    height: 250px;
    margin: 15px 0;
  }

  .typography h1, .typography h2 {
    margin: 25px 0 19px;
  }

  blockquote {
    margin: 1.875rem 0 1.875rem 2.5rem;
    padding: 1.125rem 0;
  }
  blockquote:before {
    left: -75px;
    width: 50px;
    top: 1.125rem;
    height: 45px;
    background-size: 100%;
  }
  blockquote:after {
    height: 45px;
    width: 50px;
    background-size: 100%;
  }
  blockquote:before, .typography blockquote:before {
    left: -12px;
  }
  blockquote:before, .typography blockquote:before, blockquote:after, .typography blockquote:after {
    width: 50px;
    height: 50px;
  }
  blockquote:after, .typography blockquote:after {
    background-size: 2.5rem;
    left: 70px;
  }
  blockquote, .typography blockquote {
    padding: 35px 0 1.875rem;
  }
}
