/*============================================================================
	#general
==============================================================================*/


$ui-step: 4px;

@mixin ui-space($direction, $size) {
    @if ($direction == top) {
        margin-top: $ui-step * $size;
    }
    @if ($direction == bottom) {
        margin-bottom: $ui-step * $size;
    }
    @if ($direction == left) {
        margin-left: $ui-step * $size;
    }
    @if ($direction == right) {
        margin-right: $ui-step * $size;
    }
    @if ($direction == all) {
        margin: $ui-step * $size;
    }

}


//------------------            text size          ----------------------------

@mixin size-text($class, $size, $line) {
    .size-#{$class} {
        font-size: calc(#{$size}rem / 16);
        line-height: calc(#{$line}rem / 16);

        h1, h2, h3, h4, h5, p, i, span, li, a, blockquote, p > a, i > a, span > a, li > a, blockquote > a, ol > li, ol > li > a, ul > li, ul > li > a, ol, ul {
            font-size: calc(#{$size}rem / 16);
            line-height: calc(#{$line}rem / 16);
        }
    }
}

@mixin color-text($class) {
    #{'.color-' + $class} {
        h1, h2, h3, h4, h5, label, p, i, span, li, a, blockquote, p > a, i > a, span > a, li > a, blockquote > a, ol > li, ol > li > a, ul > li, ul > li > a, ol, ul {
            @content;
        }
    }
}


//color-black
@include color-text(black) {
    color: #0B0B0B;
}

//color-blue
@include color-text(purple) {
    color: #7339F5;
}
//color-white
@include color-text(white) {
    color: #fff;
}

$colorSecondary: #7339F5;

