.footer {
  position: relative;
  overflow: hidden;

  &__top {
    background: #0D0F24;
    padding: 36px 0 24px;
    @media (max-width: 767.98px) {
      padding: 56px 0 24px;
    }
  }

  &__top-content {
    display: flex;
    @media (max-width: 767.98px) {
      flex-wrap: wrap;
      align-items: center;
      justify-content: space-between;
    }
  }

  &__logo {
    min-width: 85px;
    width: 85px;
    height: 85px;
    border-radius: 6px;
    margin-right: 209px;
    background: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    @media (max-width: 1199.98px) {
      margin-right: 150px;
    }
    @media (max-width: 1023.98px) {
      margin-right: 70px;
    }
    @media (max-width: 767.98px) {
      width: 58px;
      height: 58px;
      min-width: 58px;
      margin-right: 24px;
    }

    a {
      width: 60px;
      height: 60px;
      display: flex;
      align-items: center;
      justify-content: center;
      @media (max-width: 767.98px) {
        width: 40px;
        height: 40px;
        min-width: 40px;
      }
      img {
        border-radius: 6px;
      }
    }
  }

  &__info {
    @media (max-width: 767.98px) {
      width: 100%;
      max-width: 230px;
      margin: 64px auto 24px;
    }
  }

  &__menu {
    margin-right: 185px;
    @media (max-width: 1199.98px) {
      margin-right: 70px;
    }
    @media (max-width: 767.98px) {
      margin-right: 0;
    }

    .menu__item {
      padding: 0;
      @media (max-width: 767.98px) {
        width: 50%;
      }
    }

    .menu {
      display: block;
      margin: 0;
      @media (max-width: 767.98px) {
        display: flex;
        flex-wrap: wrap;
        min-width: 200px;
      }

      a.menu__link {
        color: #FFF;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        padding: 0 0 11px 0;
        line-height: 14px;
      }

      .menu__link.active {
        color: #B87CFF;
        border-bottom: 0;
      }
    }
  }

  &__email {
    margin-bottom: 15px;
    @media (max-width: 767px) {
      margin-bottom: 24px;
    }

    a {
      display: flex;
      align-items: center;
      transition: 0.5s;

      svg {
        width: 16px;
        height: 16px;
        margin-right: 10px;
      }

      color: #FFF;
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
    }
  }

  &__content {
    display: flex;
    justify-content: space-between;
  }

  &__location {
    width: 210px;

    p {
      display: flex;

      .icon {
        min-width: 16px;
        margin-right: 8px;
      }

      svg {
        width: 16px;
        height: 16px;
      }

      color: #FFF;
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
    }
  }

  &__socials {
    display: flex;
    margin-top: -15px;
    @media (max-width: 767.98px) {
      margin: 27px auto 0;
      width: 100%;
      justify-content: center;
    }
  }


  &__phone {
    margin-bottom: 10px;
    display: flex;
    width: auto;
    flex: 1 1 auto;
    justify-content: flex-end;
    flex-wrap: wrap;
    @media (max-width: 767.98px) {
      justify-content: flex-start;
    }

    a {
      display: flex;
      justify-content: flex-end;
      width: 100%;
      @media (max-width: 767.98px) {
        justify-content: flex-start;
        max-width: 230px;
        margin: 0 auto;
      }

      span {
        color: #FFF;
        font-size: 18px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
        transition: 0.5s;
      }

      svg {
        width: 16px;
        height: 16px;
        margin-right: 6px;
      }
    }
  }

  &__social-icon {
    width: 24px;
    height: 24px;
    margin-right: 12px;

    a svg {
      width: 24px;
      height: 24px;
      margin-right: 0;


    }

    a {
      @media (max-width: 767.98px) {
        padding-left: 0;
      }
    }

    &:last-child {
      margin-right: 0;
    }
  }

  &__bottom {
    background: #000;
    padding: 16px 0 15px;

    p {
      color: #FFF;
      font-size: 12px;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
      opacity: 0.6;
      text-align: center;
    }
  }
}

.footer__social-icon a svg path {
  transition: 0.5s
}

@media (max-width: 4500.98px) and (min-width: 1023.98px) {
  .footer__menu .menu a.menu__link:hover {
    border: 0;
    color: #B87CFF;
  }
  .footer__phone a:hover span, .footer__email a:hover {
    color: #B87CFF;
  }
  .footer__social-icon a:hover svg path {
    stroke: rgba(184, 124, 255, 1);
  }
  .footer__socials .footer__social-icon:last-child a:hover svg path:first-child {
    fill: rgba(184, 124, 255, 1);
  }
}