//  language   //
.header__language {
  width: 55px;
  margin-right: 68px;
  @media (max-width: 1199.98px) {
    margin-right: 30px;
  }
}

.header__currency-block {
  margin-top: 2px;
  margin-right: 48px;
}

.language-dropdown {
  position: relative;
}

.language-dropdown__button {
  padding: 0;
  background: inherit;
  border: 0;
  list-style: none;
  margin: 0 auto;
  display: inline-block;
  position: relative;
  text-decoration: none;
  transition: .5s;
  color: #FFF;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  width: 55px;
  text-align: left;
}

.language-dropdown svg {
  position: absolute;
  right: 0;
  top: 0;
  z-index: -1;
  transition: 0.5s;
}

.language-dropdown__list {
  border-radius: -27px;
  position: absolute;
  margin-left: -29px;
  transform: translateY(10px);
  opacity: 0;
  visibility: hidden;
  transition: all .2s linear;
  margin-top: 12px;
  padding: 22px 29px;
  background: rgba(13, 15, 36, 1);
  width: 91px;
  height: auto;
}

.language-dropdown.show svg {
  transform: rotate(180deg);
  top: -3px;
}

.language-dropdown__item {
  display: block;
  padding: 0;
  width: 100%;
  text-align: left;
  background: inherit;
  border: 0;
  transition: all .5s;
  margin-bottom: 12px;

  &:last-child {
    margin-bottom: 0;
  }
}

.language-dropdown__list .active div, .language-dropdown__list .active a {
  color: #B87CFF;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.language-dropdown__list div:hover {
  cursor: pointer;
}

.language-dropdown__list.show:before {
  content: '';
  position: absolute;
  top: -8px;
  left: 50%;
  transform: translateX(-50%);
  width: 0;
  height: 0;
  border: 0 solid transparent;
  border-right-width: 10px;
  border-left-width: 10px;
  border-bottom: 10px solid rgb(13, 15, 36);

}

.language-dropdown__item a, .language-dropdown__item div {
  color: #FFF;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  display: block;
}

.language-dropdown__list.show {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
  z-index: 2;
}

.language-dropdown.show .button.currency-dropdown__button {
  color: #000;
  font-weight: 700;
}

.language-dropdown .language-dropdown__button:hover {
  cursor: pointer;
}


//   language   //


.header__mobile-logo {
  position: relative;
}

.mobile-menu__content {
  display: none;
  @media (max-width: 1023.98px) {
    display: flex;
    align-items: center;
  }

  .mobile-menu__phone {
    width: 28px;
    margin-right: 46px;
  }
}

.top-menu {
  @media (max-width: 1023.98px) {
    display: flex;
    justify-content: space-between;
    width: 100%;
  }
}

.header {
  position: fixed;
  left: 50%;
  transform: translateX(-50%);
  z-index: 99;
  width: 100%;
  max-width: 1920px;

  &__mobile-wrapper {
    display: flex;
    align-items: center;
    @media (max-width: 1023.98px) {
      display: block;
      height: auto;
      position: relative;
      min-height: 100vh;
      background-color: #0D0F24;
      transition: 0.5s;
    }
  }

  &__logo {
    width: 85px;
    height: 85px;
    background: #fff;
    display: flex;
    align-items: center;
    justify-content: center;

    a {
      width: 60px;
      height: 60px;
      display: block;
      @media (max-width: 1023.98px) {
        width: 40px;
        height: 40px;
      }
    }

    @media (max-width: 1023.98px) {
      width: 56px;
      height: 56px;
    }
  }

  &__wrapper {
    max-width: 1140px;
    z-index: 4;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    @media (max-width: 1023.98px) {
      display: flex;
      justify-content: space-between;
      padding: 0 16px;
    }
  }

  &__phone {
    margin-right: 47px;

    a {
      letter-spacing: 0.05em;
      transition: 0.5s;
      color: #0b1f2f;
    }
  }

  &__email {
    a {
      letter-spacing: 0.05em;
      transition: 0.5s;
      color: #0b1f2f;
    }
  }

  &__top {
    display: flex;
    justify-content: space-between;
    border-bottom: 1.5px solid rgba(255, 255, 255, 0.2);
    padding: 4px 0 13px;
    align-items: center;
    height: 54px;
  }

  &__popup {
    width: 172px;
    height: 28px;
    background: #ffffff;
    border-radius: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    letter-spacing: 0.05em;
    font-weight: 500;
    margin: 6px -12px 0 48px;
  }

  &__bottom {
    border-bottom: 1.5px solid rgba(255, 255, 255, 0.2);
    display: flex;
    padding: 5px 0 5px;
  }

  &__top-left {
    display: flex;
    padding-left: 218px;
    align-items: center;
  }

  &__mobile-logo {
    margin-left: 8px;
  }

  &__socials {
    display: flex;
    align-items: center;
    margin-right: 68px;
    @media (max-width: 1199.98px) {
      margin-right: 30px;
    }
    @media (max-width: 1023.98px) {
      padding-bottom: 150px;
    }
  }

  &__mobile-block {
    @media (max-width: 1023.98px) {
      position: fixed;
      left: 0;
      width: 100%;
      background-color: #0D0F24;
      text-align: center;
      top: 0;
      height: 100vh;
      transition: 0.5s;
      transform: translateY(-150%);
    }
  }

  &__social-icon {
    width: 24px;
    height: 24px;
    margin-right: 12px;

    &:last-child {
      margin-right: 0;
    }

    a {
      width: 100%;
      height: 100%;
      display: block;

      .svg {
        width: 100%;
        height: 100%;
        transition: 0.5s;

        path {
          transition: 0.5s;
        }
      }
    }

  }
}


.menu {
  padding: 0;
  position: relative;
  display: flex;
  margin: 0 45px 0 91px;
  @media (max-width: 1199.98px) {
    margin: 0 40px 0 40px;
  }


  &__item {
    position: relative;
    list-style: none;
    padding: 0 22px;
  }

  &__link {
    display: block;
    transition: 0.5s;
    font-size: 16px;
    line-height: 16px;
    font-style: normal;
    font-weight: 400;
    padding: 33px 0;
    color: #fff;

    &:hover {
      cursor: pointer;
    }
  }

}

.header-popup {
  width: 182px;
  height: 40px;

}

@media (max-width: 4500.98px) and (min-width: 1199.98px) {
  .no-scroll-popup .header__wrapper {
    width: 1140px;
  }
  .no-scroll-popup .header__social {
    margin-right: -9px;
  }
  .header__social-icon a:hover svg path {
    stroke: rgba(184, 124, 255, 1);
  }
  .menu__link:hover {
    color: #B87CFF;
    border-bottom: 2px solid #8C3EEA;
  }
  .header__social-icon a svg path {
    transition: 0.5s
  }
}


.menu__link.active {
  color: #B87CFF;
  border-bottom: 2px solid #8C3EEA;
}

@media (max-width: 4500.98px) and (min-width: 1199.98px) {
  .header__social-icon:hover svg path,
  .header__social a:hover .icon-svg path {
    fill: #rgba (184, 124, 255, 1);
  }

}


#header__burger-menu .icon-menu-close {
  display: none;
}

.header__mobile.fixed {
  #header__burger-menu .icon-menu-close {
    display: block;
  }

  #header__burger-menu .icon-menu {
    display: none;
  }
}

.header__mobile-bg.fixed {
  background-color: #0D0F24;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  // height: 100vh;
  z-index: 1;
  transition: 0.2s;
  animation: arrow 0.8s forwards;
}

.header__mobile.fixed .header__mobile-bg {
  background-color: #d8e1e7;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  // height: 100vh;
  //transition: 0.2s;
  animation: arrow 0.4s forwards;
  animation-delay: 0.2s;
  z-index: 99999;
}


.header__mobile.closeMenu {
  position: fixed;
  display: block;
  top: 0;
  left: 0;
  height: 100vh;


  .header__mobile-content {
    height: 100vh;
  }

  .header__mobile-bg {
    background-color: #d8e1e7;
  }
}

.header__mobile.closeMenu .header__mobile-bg {
  opacity: 1;
}


.header__mobile-content {
  display: none;
}

.header__mobile-modal:hover {
  &:before {
    background-color: #c3aa93;
  }
}

.header__mobile-modal {
  width: 100%;
  padding: 6px;

  span {
    line-height: 16px;
  }
}

.header {
  height: 85px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  margin: 0 auto;
  @media (max-width: 1023.98px) {
    height: 56px;
  }

}

.header.header-down,
.header.header-up {
  background: #0B0B0B;
}

.header__popup-mobile, .header__mobile-close {
  display: none;
}


@media (max-width: 1023px) {
  .header__mobile-block.open {
    display: block;
    transform: translateY(0%);
    overflow: auto;
    min-height: 100vh;
    height: 100%;
  }

  .header__mobile-close {
    display: block;
    position: absolute;
    top: 27px;
    right: 27px;
    width: 16px;
    height: 16px;
  }

  .menu {
    display: block;
  }

  .menu__link {
    color: #FFF;
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    padding: 20px 0;
    text-align: left;
  }

  .menu__link.active {
    border: 0
  }

  .header__socials {
    margin-right: 0;
    margin-left: 62px;
    margin-top: 70px;
  }

  .language-dropdown {
    margin-left: 62px;
    margin-right: 0;
    position: absolute;
    bottom: 93px;
  }

  .language-dropdown__list {
    background: inherit;
    margin-left: 0;
    opacity: 1;
    visibility: visible;
    margin-top: 0;
    padding: 0;

  }
  .header__menu {
    padding-top: 90px;
  }
  .language-dropdown__button, .language-dropdown svg {
    display: none;
  }
  .right-block {
    display: none;
  }
}

@media (max-width: 767.98px) {
  .header__mobile {
    height: 70px;
  }
  .header__mobile.fixed .header__top {
    padding-right: 15px;
  }
  .header__mobile.fixed .header__top,
  .header__mobile.fixed .menu {
    padding-left: 15px;
  }
  .header__mobile.fixed .header__top {
    height: 480px;
  }

  .header__mobile.fixed .header__top .header__top-left {
    padding-bottom: 120px;
    margin-top: -100px;
  }
  .header__mobile.header__other.fixed .header__wrapper .header__bottom {
    margin-bottom: -40px;
  }
}

@media (max-width: 374.98px) {
  .header__mobile.fixed .header__popup-mobile {
    position: relative;
    left: 0;
    order: 5;
    width: 100%;
    top: 25px;
  }

  .header__mobile.fixed .header__top .header__top-left {
    padding-bottom: 85px;
  }

}
