.breadcrumbs__list {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  max-width: 90%;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  margin: 0;
  padding-left: 0;
}
.breadcrumbs__item {
  padding-right: 8px;
  font-size: 1.4rem;
  font-weight: 600;
  line-height: 1.8rem;
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-right: 24px;
  letter-spacing: 0;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-flex: 0;
  -ms-flex: 0 0 auto;
  flex: 0 0 auto;
}
.breadcrumbs__link {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-transition: .5s;
  -o-transition: .5s;
  transition: .5s;
  text-decoration: none;
  color: #0B0B0B;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  @media (max-width: 1199.98px) {
    font-size: 12px;
  }
  &:hover{
    color: #B87CFF;
  }
}
.breadcrumbs__item.current {
  display: block;
  overflow: hidden;
  min-width: 100px;
  max-width: 300px;
  white-space: nowrap;
  text-overflow: ellipsis;
  -o-text-overflow: ellipsis;
  -webkit-box-flex: 1;
  -ms-flex: 1;
  flex: 1;
  color: #7B7B7B;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}
.breadcrumbs__item::after {
  position: absolute;
  content:'';
  top: 5px;
  right: 0;
  width: 8px;
  height: 8px;
  display: block;
  margin-right: -8px;
  -webkit-transition: .5s;
  -o-transition: .5s;
  transition: .5s;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
  pointer-events: none;
  border-top: 1px solid #0B0B0B;
  border-right: 1px solid #0B0B0B;
}
.breadcrumbs__item.current:after{
  display: none;
}