.section-pagination {
  padding-bottom: 105px;
  margin-top: 50px;
  @media (max-width: 767.98px) {
    padding-bottom: 57px;
  }
  &__content {
    display: flex;
    justify-content: center;
    padding: 0;
  }

  .nav-item {
    list-style-type: none;
    transition: 0.5s;
    margin: 0 5px;
    border-radius: 4px;
    &:hover {
      @media (max-width: 4500.98px) and (min-width: 1199.98px) {
        border-radius: 4px;
        background: #7339F5;
        a.number{
          color: #fff;
        }
      }
    }
    a.number {
      color: #0B0B0B;
      font-size: 24px;
      font-style: normal;
      font-weight: 400;
      line-height: 155%;
      padding: 2px 10px;
    }
  }

  .active {
    background: #7339F5;
    a.number{
      color: #fff;
    }
  }
}