.modal {
  display: none;
}

.modal .modal-bg {
  display: block;
  background: rgba(0, 0, 0, 0.60);
  backdrop-filter: blur(10px);
  width: 100%;
  z-index: -1;
  position: fixed;
  top: 0;
  max-width: 1920px;
  left: 50%;
  transform: translateX(-50%);
  overflow: auto;
  height: 100%;
}

#main-collection .modal__wrapper {
  padding-bottom: 40px;
}

.modal__wrapper {
  width: 399px;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  padding: 0;
  z-index: 1;
  border-radius: 10px;
  background: #E4E4EF;
}

.modal__block {
  padding: 35px 39px 40px;
  @media (max-width: 767.98px) {
   padding: 82px 19px 40px;
  }
}

.form__messages {
  margin-left: 3px;
  p {
    color: #464646;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;

    span {
      color: #7339F5;
    }
  }
}

.modal__header {
  position: relative;
  z-index: 1;
  margin-bottom: 8px;
}

.modal .main-form {
  margin-top: 0;
}


.modal__header .modal-close {
  position: absolute;
  width: 30px;
  height: 30px;
  top: -19px;
  right: -16px;
  background: initial;
  border: 0;
  z-index: 3;
}

.modal__header .modal-close svg {
  width: 30px;
  height: 30px;
}

.modal__header .modal-close:hover {
  cursor: pointer;
}

.modal__title p {
  color: #0B0B0B;
  text-align: center;
  font-size: 24px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  margin-bottom: 16px;
  padding: 0 20px;
}

#popup-thanks .wrapper {
  height: 176px;
}

#popup-thanks .modal__title p {
  font-size: 32px;
  line-height: 40px;
  color: #FCAE20;
  margin-bottom: 15px;
}

@media (max-width: 767px) {

  .modal__title {
    font-size: 28px;
    line-height: 34px;
  }
}



.modal .main-form {
  width: 100%;
}

.modal .main-form .button-send span {
  text-transform: initial;
}

.modal .main-form .button-send {
  width: 100%;
  border: 0;
  margin-top: 37px;
  border-radius: 30px;
  background: var(--button, linear-gradient(90deg, #903BE9 0%, #406EF7 100%));
  transition: 0.5s;
}

@media (max-width: 4500.98px) and (min-width: 1199.98px) {
  .modal .main-form .button-send:hover {
    border-radius: 30px;
    background: linear-gradient(90deg, #6014AF 0%, #1342CE 100%);
  }
}

.object__checkbox .object__filter-title-item {
  color: rgba(62, 72, 96, .8);
  font-size: 15px;
  line-height: 18px;
  position: relative;
  padding-left: 30px;
  margin-bottom: 15px;
  width: 100%;
}

.object__filter-title-item input {
  position: absolute;
  width: 0;
  height: 0;
  cursor: pointer;
  opacity: 0;
}

.modal .main-form .form__row {
  width: 100%;
  margin-right: 0;
  position: relative;
}

.modal .form-control label {
  display: flex;
  margin-bottom: 7px;
  align-items: center;

  svg {
    width: 18px;
    height: 18px;
    margin-right: 8px;
  }

  span {
    color: #0B0B0B;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    padding-left: 27px;

    span {
      color: #7339F5;
      padding-left: 0;
    }
  }
}

.modal.modals__modal input {
  box-sizing: border-box;
  width: 100%;
  border-radius: 4px;
  background: #FFF;
  height: 44px;
  color: #0B0B0B;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
  border: transparent;
  transition: 0.5s;
  padding: 4px 5px 8px 24px;
}

.modal .main-form .form__row:last-child {
  margin-right: 0;
  margin-bottom: 0;
}

.modals__modal input:active, .modals__modal input:hover, .modals__modal input:focus {
  border: 1px solid #7339F5;
}
input.just-validate-error-field {
 border-color: #b81111;
}
.modals__modal input::placeholder {
  font-weight: 500;
  font-size: 15px;
  line-height: 22px;
  font-feature-settings: 'pnum' on, 'lnum' on;
  color: #BED4FF;
  display: flex;
  align-items: center;

}

.modal .main-form__input-group .req-star {
  top: 10px;
}

.main-form__input-group .req-star {
  position: absolute;
  width: 9px;
  height: 16px;
  top: -16px;
  right: 0;
}

.modal .main-form__input-group {
  position: relative;
}

textarea {
  padding: 13px 25px;
  width: 100%;
  height: 100%;
  max-width: 100%;
  font-size: 14px;
  line-height: 24px;
  letter-spacing: 0.04em;
  color: rgba(0, 0, 0, 0.35);
  min-height: 134px;
  min-width: 100%;
  background: rgba(255, 255, 255, 0.26);
  border: 1px solid rgba(0, 0, 0, 0.47);
  box-sizing: border-box;
  border-radius: 20px;
}

textarea:active {
  border: 1px solid #fff;
}

textarea:focus, textarea:focus-visible {
  border: 1px solid #fff;
  outline: 0;
}


@media (max-width: 480px) {
  .main-form .button-send {
    width: 100%;
  }
}

.form__row select {
  width: 100%;
  height: 60px;
  padding: 20px 25px;
  border: 1px solid #BED4FF;
  background: inherit;
  -webkit-border-radius: 15px;
  -moz-border-radius: 15px;
  border-radius: 15px;
  font-weight: 500;
  font-size: 15px;
  line-height: 22px;
  /* or 147% */

  font-feature-settings: 'pnum' on, 'lnum' on;

  color: #BED4FF;
}

.form__row {
  position: relative;
}

.form em.error {
  font-size: 12px;
  line-height: 18px;
  position: absolute;
  left: 26px;
  display: inline-block;
  width: 100%;
  margin-top: 0;
  top: 48px;
  padding-left: 0;
  color: red;
  font-style: normal;
}

@media (max-width: 767px) {
  .form em.error {
    top: 38px;
    font-size: 11px;
  }
}

.modal .form em.error {
  top: 48px;
}


@media (max-width: 767px) {
  #thanks .modal__wrapper {
    max-height: 220px;
  }
}

@media (max-width: 767px) {
  #thanks .modal__wrapper .modal__title {
    margin-bottom: 10px;
  }
}

#thanks .modal__wrapper p {
  padding: 0;
  margin-bottom: 24px;
}
.modal__text p{
  color: #0B0B0B;
  text-align: center;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
}
.modal__header .modal-close.modal__close-btn{
  position: relative;
  top: inherit;
  right: inherit;
  height: 50px;
  border-radius: 30px;
  background: var(--button, linear-gradient(90deg, #903BE9 0%, #406EF7 100%));
  width: 100%;
  margin-top: 40px;
  transition: background 2s ease;
  &:hover{
    background: linear-gradient(90deg, #6014AF 0%, #1342CE 100%);
  }
}
@media (max-width: 767px) {
  #thanks .modal__wrapper p {
    padding: 0 20px;
  }
}

.form__bottom {
  display: flex;
  justify-content: space-between;
}

.form__bottom a {
  font-size: 14px;
  line-height: 24px;
  letter-spacing: 0.04em;
  color: #858585;
}

.form__row-checkbox {
  margin-left: 26px;
  font-size: 13px;
  line-height: 18px;
  color: #25282e;
}

.main-form__consent {
  padding-left: 0;
}

.main-form__consent span {
  font-size: 14px;
  line-height: 24px;
  letter-spacing: 0.04em;
  color: #858585;
}

.main-form__consent input {
  position: absolute;
  width: 0;
  height: 0;
  cursor: pointer;
  opacity: 0;
}


.modal.active {
  display: block;
  width: 100%;
  z-index: 9999;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
  height: 100%;
}
.modal__back-arrow{
  display: none;
}
.modal .form em.error {
  top: 48px;
}

@media (max-width: 1023.98px) {
  .modal__wrapper {
    width: 500px;
    max-width: 500px;
  }
}

@media (max-width: 767px) {
  .modal__wrapper {
    width: 100%;
    height: 100vh;
  }
  .modal__back-arrow{
    position: absolute;
    display: flex;
    top: -70px;
    left: 0;
    color: #252525;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 34px;
    align-items: center;
    svg{
      margin-right: 8px;
    }
  }
  .modal__header .modal-close:after {
    width: 60px;
    height: 60px;
    top: 10px;
    left: 9px;
  }
  .modal__content {
    width: 100%;
  }
  .modal__header .modal-close {
    right: 15px;
    padding: 0;
  }
  .modal__content-bg {
    position: absolute;
    top: -16px;
    left: 50%;
    width: 236px;
    transform: translateX(-50%);
  }
  .modal .main-form .button-send {
    margin-top: 40px;
  }
  .modal__wrapper {
    top: 0;
    border-radius: 0;
  }
  .modal__header .modal-close {
    top: -70px;
  }
}

@media (max-width: 767.98px) {
  .modal__title p {
    font-size: 24px;
    line-height: 30px;
    padding: 0;
  }
}

@media (max-width: 480.98px) {
  .modal .main-form .button-send {
    font-size: 14px;
  }
  .form__row-checkbox {
    margin-left: 28px;
  }
  .form__checked {
    width: 100%;
    margin: 20px auto 0;
  }
  .modals__modal input {
    height: 40px;
  }
  .modal .form em.error {
    top: 40px;
  }
}
