.btn {
    border: 0;
    border-radius: 30px;
    background: var(--button, linear-gradient(90deg, #903BE9 0%, #406EF7 100%));
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: background-color .5s;

    &:hover{
        background: linear-gradient(90deg, #6014AF 0%, #1342CE 100%);
    }
    span{
        font-size: 18px;
        font-style: normal;
        font-weight: 400;
        line-height: 32px;
        letter-spacing: 0.5px;
        color: #fff;
    }
    &:hover {
        cursor: pointer;
    }
}
.btn-l{
    border-radius: 30px;
    border: 1px solid var(--button, #903BE9);
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 0.5s;
    height: 40px;
    span{
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: 32px;
        letter-spacing: 0.5px;
        color: #fff;
    }
    &:hover {
        cursor: pointer;
        border: 1px solid var(--button, #903BE9);
        background: var(--button, linear-gradient(90deg, #903BE9 0%, #406EF7 100%));

    }
}
@media (max-width: 4500.98px) and (min-width: 1023.98px) {
    .btn:hover {
        color: #191b1d;
    }
}
